import one from './media/images/software.jpg';
import two from './media/images/android.png';
import three from './media/images/windows.png';
import four from './media/images/cat.png';
// import "./css/home.css";
import { BrowserRouter as Router, Link } from "react-router-dom";

function Home() {
  return (
    <>
      <div className="container-fluid">
        <div className="row align-items-center">
            <div className="mx-auto col-lg-6 col-md-6 align-items-center">
                <img src={one} className="img-fluid"
                    alt="..." />
            </div>
            <div className="text-center col-lg-6 col-md-6 container-fluid">
                <h1 id="mast">Deeptam Research Foundation</h1>
                <p><b>Get Softwares Here by just Clicking on Get Softwares</b></p>
                <Link to="categories/"
                    className="inline-flex text-white bg-indigo-500 border-0 py-2 px-6 focus:outline-none hover:bg-indigo-600 rounded text-lg btn-danger btn mb-3">Get
                    Softwares</Link>
            </div>
        </div>
    </div>

    <div className="container">
        <div className="row mb-3">
            <div className="card mx-auto my-2 p-2 align-items-center w-18">
                <img src={two} className="padded card-img-top" alt="..." />
                <div className="card-body">
                    <h5 className="card-title text-center">Android</h5>
                    <p className="card-text text-center">
                        Want Android Softwares. Click on Get Android
                    </p>
                    <Link to="apps/" className="btn btn-primary d-block">Get Android Apps</Link>
                </div>
            </div>

            <div className="card mx-auto my-2 p-2 align-items-center w-18">
                <img src={three}
                    className="padded card-img-top" alt="..." />
                <div className="card-body">
                    <h5 className="card-title text-center">Windows</h5>
                    <p className="card-text text-center">
                        Want windows Softwares. Click on Get Windows Softwares
                    </p>
                    <Link to="windows-softwares/" className="btn btn-primary d-block">Get Windows Softwares</Link>
                </div>
            </div>

            <div className="card mx-auto my-2 p-2 align-items-center w-18">
                <img src={four}
                    className="padded card-img-top" alt="..." />
                <div className="card-body">
                    <h5 className="card-title text-center">Categories</h5>
                    <p className="card-text text-center">
                        Want Softwares with his Categories. Click on Categories
                    </p>
                    <Link to="categories/" className="btn btn-primary d-block">Categories</Link>
                </div>
            </div>
        </div>
    </div>
    </>
  );
}

export default Home;